<template>
  <div class="top">
    <div>
      <van-row type="left" gutter="20" justify="space-between">
        <van-col span="12" style="text-align: left">
          <span class="blue_line"></span>
          <span class="plan_title">被保险人信息</span>
        </van-col>
      </van-row>
    </div>

    <div class="underwriter" v-if="!InformationFormJSON">
      <div class="information" @click="informationModify">
        <div class="information_name">被保险人1</div>
        <div class="information_Modify" >
          填写信息
          <span>
            <van-icon size="20px" color="#4CA2FE " style="vertical-align: bottom" name="arrow" />
          </span>
        </div>
      </div>
    </div>

    <div class="underwriter" v-for="(item, index) of InformationFormJSON" :key="index">
      <div class="information">
        <div class="information_name">{{ item.userName }}</div>
        <div class="information_self">
          <span> {{ item.ChoosePlan }} </span>
        </div>
        <div class="information_plan">
          <span> {{ item.whomInsure }} </span>
        </div>
        <div class="information_Modify">
          修改信息
          <span>
            <van-icon size="20px" color="#4CA2FE " style="vertical-align: bottom" name="arrow" />
          </span>
        </div>
      </div>
      <div class="Card_Information">
        <div class="card_Font">身份证号</div>
        <div class="card_num">{{ item.idCard }}</div>
      </div>
    </div>

    <div class="add_but">
      <van-button class="button_immediately" type="info" icon="plus" plain @click="addInsured">
        添加被保险人</van-button>
    </div>

    <div class="Contact_Information">
      <van-row type="left" gutter="20" justify="space-between">
        <van-col span="12" style="text-align: left">
          <span class="blue_line"></span>
          <span class="plan_title">联系信息</span>
        </van-col>
      </van-row>
      <div class="van_row">
        <van-row type="left" gutter="20" justify="space-between">
          <van-col span="12" style="text-align: left">居住地区</van-col>
          <van-col span="12" style="text-align: right" @click="FillInformation">
            <div class="information_Modify">
              <span :class="[
                Addform.ResidentialAddress == '请选择'
                  ? 'ResidentialAddressClass2'
                  : 'ResidentialAddressClass',
              ]">
                {{ Addform.ResidentialAddress }}</span>
              <span>
                <van-icon v-if="Addform.ResidentialAddress == '请选择'" size="20px" color="#4CA2FE "
                  style="vertical-align: bottom" name="arrow" />
              </span>
            </div>
          </van-col>
        </van-row>
      </div>
      <!-- 居住地址 -->
      <van-popup v-model="showResidentialAddress" position="bottom">
        <van-area :area-list="areaList" @confirm="onConfirm" @cancel="showResidentialAddress = false" />
      </van-popup>

      <div class="Full_address">
        <div class="address">
          详细地址
          <!--  点击详细信息后面的图标 -->
          <van-popup v-model="showAddress">上海市浦东新区金海路1000号</van-popup>

          <van-icon @click="showAddressClick" name="question-o" size="20px" color="#4CA2FE "
            style="vertical-align: bottom; margin-left: 1vh" />
        </div>
        <van-field v-model="Addform.inputValue" label="" placeholder="请输入详细地址"></van-field>
      </div>
    </div>

    <div class="bomm_but">
      <van-button class="button_Insurance" type="info" @click="nexSttep">下一步</van-button>
    </div>
    <!-- 录入信息弹框 -->
  </div>
</template>

<script>
// 导入中国省市区
import areaList from "../../../../views/card/student-card/views/informationEntry/components/areaList";
export default {
  name: "",
  components: {},

  data() {
    return {
      InformationFormJSON: JSON.parse(
        sessionStorage.getItem("InformationForm_confirm")
      ),
      // 点击详细信息后面的图标
      showAddress: false,

      Addform: {
        // 请选择
        ResidentialAddress: "请选择",
        // 请输入详细地址
        inputValue: "",
      },

      // pop弹框
      showResidentialAddress: false,
      // 全国地址
      areaList: areaList, // 数据格式见 Area 组件文档
    };
  },

  created() { },
  mounted() {
    if (JSON.parse(sessionStorage.getItem("Addform"))) {
      this.Addform = JSON.parse(sessionStorage.getItem("Addform"));
    }
  },
  methods: {
    // 点击填写信息
    informationModify() {
      // 跳转到信息录入页面
      this.$router.push({
        path: "/card/InformationForm",
      });
    },
    // 点击详细信息后面的图标
    showAddressClick() {
      this.showAddress = true;
    },
    // 添加被保险人
    addInsured() {
      this.$router.push({
        path: "/card/InformationForm",
      });
    },
    // 点击请选择
    FillInformation() {
      this.showResidentialAddress = true;
    },
    // 点击下一步
    nexSttep() {
      if (
        this.Addform.ResidentialAddress != "" &&
        this.Addform.ResidentialAddress != "请选择"
      ) {
        if (!this.Addform.inputValue) {
          this.$toast("请填写详细地址");
        } else {
          // 跳转到产品条款
          if (this.InformationFormJSON) {
            sessionStorage.setItem("Addform", JSON.stringify(this.Addform));
            this.$router.push({
              path: "/card/cardProductTerms",
            });
          } else {
            this.$toast("请添加被保险人！");
          }
        }
      } else {
        this.$toast("请选择居住地址");
      }
    },
    // 弹框的确认
    onConfirm(values) {
      this.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join("-");
      this.Addform.ResidentialAddress = this.value;
      this.showResidentialAddress = false;
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='less' scoped>
.top {
  margin-top: 5vh;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
}

.plan_title {
  font-size: 18px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 600;
  color: #303133;
}

.blue_line {
  display: inline-block;
  width: 6px;
  height: 3vh;
  background: #4ca2fe;
  border-radius: 0px 0px 0px 0px;
  opacity: 1;
  margin-right: 5px;
  vertical-align: bottom;
}

.underwriter {
  text-align: left;
  display: flex;
  flex-direction: column;
  padding: 1vh;
  margin-top: 3vh;
  background: #ffffff;
  box-shadow: 0px 0px 6px 1px rgba(159, 159, 159, 0.16);
  border-radius: 8px 8px 8px 8px;
  opacity: 1;
}

.information {
  padding: 1vh;
  display: flex;
  justify-content: space-between;
}

.Card_Information {
  display: flex;
}

.information_name {
  font-size: 16px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 500;
  width: 12vh;
  color: #303133;
}

.information_self {
  width: 54px;
  height: 25px;
  background: #4ca2fe;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px 4px 4px 4px;
  opacity: 0.7;

  span {
    color: #fff;
  }
}

.information_plan {
  width: 54px;
  height: 25px;
  background: #4ca2fe;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px 4px 4px 4px;
  opacity: 0.7;

  span {
    color: #fff;
  }
}

.information_Modify {
  color: #4ca2fe;
}

.Card_Information {
  padding: 1vh;
  display: flex;
  justify-content: space-between;
}

.card_Font {
  font-size: 12px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #b5b5b5;
}

.card_num {
  font-size: 12px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #b5b5b5;
}

.add_but {
  margin-top: 3vh;
}

.button_immediately {
  width: 100%;
  border-radius: 18px;
}

.Contact_Information {
  margin-top: 5vh;
}

.van_row {
  margin-top: 3vh;
  padding-bottom: 3vh;
  border-bottom: 1px solid #ededed;
}

.Full_address {
  display: flex;
  justify-content: center;
  align-items: center;
}

.address {
  width: 100%;
  display: flex;
}

/deep/ .van-button--small {
  border: #fff 1px solid;
  color: #4ca2fe;
}

.ResidentialAddressClass {
  color: #303133;
}

.ResidentialAddressClass2 {
  color: #4ca2fe;
}

.bomm_but {
  margin-bottom: 50px;
}

.button_Insurance {
  // position: fixed;
  margin-top: 3vh;
  bottom: 2px;
  width: 100%;
  border-radius: 18px;
  left: 0;
}

/deep/ .van-field__control {
  text-align: right;
}
</style>
